
/* Navbar styles */
.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: rgb(0,0,0,0.3);
  padding: 10px 20px;
  position: fixed;
  top: 0;
  z-index: 2000;
  transition: background-color 0.3s ease-in-out;
  backdrop-filter: blur(10px);
}

.navbar.scrolled {
  background-color: rgba(0, 0, 0, 0.3); /* Darker background when scrolled */
}

.navbar-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

/* Left side */
.left {
  display: flex;
  align-items: center;
}

.logo-img {
  height: 40px;
  margin-right: 10px;
}

.Title {
  color: #fff;
  font-size: 24px;
  font-weight: bold;
}

/* Right side */
.right {
  display: flex;
  align-items: center;
  margin-right:20px;
}

.navbar-menu {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.navbar-item {
  color: #fff;
  font-size: 23px;
  cursor: pointer;
  transition: color 0.3s ease-in-out;
  text-decoration: none;
}
.navbar-item button{
  color: #fff;
  font-size: 23px;
  cursor: pointer;
  transition: color 0.3s ease-in-out;
  background-color: transparent;
  border: None;
}

.navbar-item:hover {
  color: #ddd;
}

.projects-menu{
  position: relative;
}
.dropdown-container{
  display: none;
  padding: 20px 20px;
  width: 200px;
  color: white;
  background-color: black;
  position: absolute;
  border-radius: 20px;
}
.arrow{
  display: inline-block;
  transition: all 0.3s ease-in-out;
  margin: 0px ;
}
.projects-menu:hover{
  .dropdown-container{
    display: block;
  }
  .arrow{
    transform: rotate(180deg);
  }
}
.dropdown-menu{
  padding: 0px;
}
.dropdown-menu li{
  list-style: none;
  transition: all 0.2s ease;
  padding: 10px;
  border-radius: 20px;
  cursor: pointer;
}
.dropdown-menu li:hover{
  background-color: gray;
}
.dropDown-item{
  text-decoration: none;
  color: white;
}


/* Responsive Styles */
@media (max-width: 768px) {
  .navbar-menu {
    display: none; /* Hide the main navbar menu */
  }

  .hamburger {
    display: block; /* Show hamburger button */
  }
}
